.fill-path {
    fill: var(--text-main);
}

.illustrate {
    max-height: 30rem;
}

@media (min-width: 320px) {
    .illustrate {
        max-height: 14rem;
    }
}

@media (min-width: 375px) {
    .illustrate {
        max-height: 16rem;
    }
}

/* @media (min-width: 440px) {
    .illustrate {
        max-height: 16rem;
    }
} */

@media (min-width: 540px) {
    .illustrate {
        max-height: 15rem;
    }
}

@media (min-width: 640px) {
    .illustrate {
        max-height: 17rem;
    }
}

@media (min-width: 768px) {
    .illustrate {
        max-height: 18rem;
    }
}

@media (min-width: 1024px) {
    .illustrate {
        max-height: 24rem;
    }
}

@media (min-width: 1280px) {
    .illustrate {
        max-height: 26rem;
    }
}

@media (min-width: 1440px) {
    .illustrate {
        max-height: 28rem;
    }
}

@media (min-width: 1530px) {
    .illustrate {
        max-height: 30rem;
    }
}
