.myImage {
    max-height: 24rem;
}

@media (min-width: 320px) {
    .myImage {
        max-height: 12rem;
    }
}

@media (min-width: 375px) {
    .myImage {
        max-height: 14rem;
    }
}

@media (min-width: 440px) {
    .myImage {
        max-height: 16rem;
    }
}

@media (min-width: 540px) {
    .myImage {
        max-height: 18rem;
    }
}

@media (min-width: 768px) {
    .myImage {
        max-height: 20rem;
    }
}

@media (min-width: 1024px) {
    .myImage {
        max-height: 22rem;
    }
}

@media (min-width: 1280px) {
    .myImage {
        max-height: 24rem;
    }
}
