/** by default its night theme [8:00 pm, 8:00 am) */

:root {
  /** layouts/bg colors */
  --color-primary: #333333; /* ! try-out = #222629 */
  --color-secondary: #444444;
  --color-footer-bg: #222222;
  --blur-bg: rgba(51, 51, 51, 0.6); /* TODO :: check */
  /** text colors */
  --text-main: #c4b37f;
  --text-mainopaque: #c4b37f0f; /** 6% opacity of main */
  --text-submain: #a8a8a8;
  --text-content: #d0d0d0;
  --text-footer: #d0d0d0;
}

/** morning theme [8:00 am, 12:00 noon) */

[data-theme="morning"] {
  --color-primary: #ffffff;
  --color-secondary: #f8ffff;
  --color-footer-bg: #c4b37f;
  --blur-bg: rgba(255, 255, 255, 0.6); /* TODO :: check */
  --text-main: #c4b37f;
  --text-mainopaque: #c4b37f0f;
  --text-submain: #8d96b2;
  --text-content: #333333;
  --text-footer: #333333;
}

/** evening theme [12.00 pm, 8.00 pm) */

[data-theme="evening"] {
  --color-primary: #ffffff;
  --color-secondary: #f8ffff;
  --color-footer-bg: #0065a3;
  --blur-bg: rgba(255, 255, 255, 0.6); /* TODO :: check */
  --text-main: #0065a3;
  --text-mainopaque: #0065a30f;
  --text-submain: #8d96b2;
  --text-content: #333333;
  --text-footer: #ffffff;
}

